import React from 'react';

const FAQs = () => {
    const faqItems = [
        {
            question: 'What is Unique Gaming Hub?',
            answer: 'Unique Gaming Hub is an interactive platform that offers a variety of engaging slot games. Our focus is on providing a simple and enjoyable gaming experience for all players.'
        },
        {
            question: 'Do I need to create an account to play?',
            answer: 'No, you do not need to create an account to play our slot games. You can start playing immediately without any registration.'
        },
        {
            question: 'Are the games free to play?',
            answer: 'Yes, all the slot games on Unique Gaming Hub are free to play. Enjoy the games without any cost or commitment.'
        },
        {
            question: 'Can I play on mobile devices?',
            answer: 'Yes, our platform is optimized for both desktop and mobile devices. You can enjoy the slot games on your smartphone or tablet as well as on your computer.'
        },
        {
            question: 'How do I contact customer support?',
            answer: 'If you have any questions or need assistance, you can contact our customer support team through the contact form on our website or by emailing us at support@uniquehubgaming.com.'
        },
        {
            question: 'Are there any age restrictions?',
            answer: 'Yes, you must be at least 18 years old to play our slot games. Please ensure that you comply with the legal age requirements in your jurisdiction.'
        }
    ];

    return (
        <div style={{ fontFamily: 'Arial, sans-serif', padding: '20px', backgroundColor: '#f9f9f9' }}>
            <header style={{ textAlign: 'center', marginBottom: '30px' }}>
                <h1 style={{ fontSize: '2.5em', color: '#333' }}>Frequently Asked Questions</h1>
            </header>

            <section style={{ maxWidth: '800px', margin: '0 auto', padding: '0 20px' }}>
                {faqItems.map((item, index) => (
                    <div key={index} style={{ marginBottom: '20px', borderBottom: '1px solid #ddd', paddingBottom: '15px' }}>
                        <h2 style={{ fontSize: '1.5em', color: '#333', marginBottom: '10px' }}>{item.question}</h2>
                        <p style={{ fontSize: '1.1em', color: '#444', lineHeight: '1.6' }}>{item.answer}</p>
                    </div>
                ))}
            </section>
            
            <footer style={{ textAlign: 'center', padding: '20px 0', borderTop: '1px solid #ddd', marginTop: '30px' }}>
                <p style={{ fontSize: '1em', color: '#666' }}>© 2024 Unique Gaming Hub. All Rights Reserved.</p>
            </footer>
        </div>
    );
}

export default FAQs;
