import React from 'react';

const About = () => {
    return (
        <div style={{ fontFamily: 'Arial, sans-serif', padding: '20px', backgroundColor: '#f9f9f9' }}>
            <header style={{ textAlign: 'center', marginBottom: '30px' }}>
                <h1 style={{ fontSize: '2.5em', color: '#333' }}>About Unique Gaming Hub</h1>
            </header>

            <section style={{ maxWidth: '800px', margin: '0 auto', padding: '0 20px' }}>
                <h2 style={{ fontSize: '2em', color: '#333', marginBottom: '15px' }}>Our Mission</h2>
                <p style={{ fontSize: '1.2em', color: '#555', lineHeight: '1.6' }}>
                    At Unique Gaming Hub, our mission is to provide a fun and engaging platform for slot game enthusiasts. We aim to offer a variety of high-quality, interactive slot games that are easy to access and enjoyable to play.
                </p>

                <h2 style={{ fontSize: '2em', color: '#333', marginTop: '30px', marginBottom: '15px' }}>What We Offer</h2>
                <p style={{ fontSize: '1.2em', color: '#555', lineHeight: '1.6' }}>
                    We offer a range of slot games designed for entertainment and relaxation. Our platform is user-friendly and optimized for both desktop and mobile devices, ensuring you can enjoy our games anytime, anywhere.
                </p>

                <h2 style={{ fontSize: '2em', color: '#333', marginTop: '30px', marginBottom: '15px' }}>Our Vision</h2>
                <p style={{ fontSize: '1.2em', color: '#555', lineHeight: '1.6' }}>
                    Our vision is to become a leading destination for slot games, known for our commitment to providing an exceptional gaming experience. We continuously strive to enhance our platform and expand our game offerings to meet the evolving needs of our players.
                </p>

                <h2 style={{ fontSize: '2em', color: '#333', marginTop: '30px', marginBottom: '15px' }}>Our Team</h2>
                <p style={{ fontSize: '1.2em', color: '#555', lineHeight: '1.6' }}>
                    Our team is dedicated to delivering the best gaming experience possible. With a passion for innovation and a focus on quality, we work hard to bring you engaging and entertaining slot games.
                </p>
            </section>

            <footer style={{ textAlign: 'center', padding: '20px 0', borderTop: '1px solid #ddd', marginTop: '30px' }}>
                <p style={{ fontSize: '1em', color: '#666' }}>© 2024 Unique Gaming Hub. All Rights Reserved.</p>
            </footer>
        </div>
    );
}

export default About;
