import React, { useState } from 'react';
import './styles/SlotGames.css';
import GamesFetcher from './GamesFetcher';

function SlotGames() {
    const [fullscreenUrl, setFullscreenUrl] = useState(null);

    const openInFullscreen = (url) => {
        setFullscreenUrl(url);
    };

    const closeFullscreen = () => {
        setFullscreenUrl(null);
    };

    return (
        <GamesFetcher>
            {({ games, error, loading }) => {
                if (loading) {
                    return (
                        <div className="loading-screen">
                            <div className="loading-dots">
                                <div></div>
                                <div></div>
                                <div></div>
                            </div>
                        </div>
                    );
                }

                if (error) {
                    return <div>{error}</div>;
                }

                return (
                    <div className="container">
                        <header className="header">
                            <h1>Discover Slot Games</h1>
                        </header>

                        <section className="gamesSection">
                            {games.map(game => (
                                <div
                                    key={game._id}
                                    onClick={() => openInFullscreen(game.url)}
                                    className="gameCard"
                                >
                                    <img
                                        src={game.imageUrl}
                                        alt={`${game.title} Logo`}
                                        className="gameLogo"
                                    />
                                    <div className="gameTitle">
                                        {game.title}
                                    </div>
                                </div>
                            ))}
                        </section>

                        {fullscreenUrl && (
                            <div className="fullscreenOverlay">
                                <iframe
                                    src={fullscreenUrl}
                                    title="Slot Game"
                                    className="fullscreenIframe"
                                />
                                <button
                                    onClick={closeFullscreen}
                                    className="closeButton"
                                >
                                    &times;
                                </button>
                            </div>
                        )}

                        <footer className="footer">
                            <p>© 2024 Unique Gaming Hub. All Rights Reserved.</p>
                        </footer>
                    </div>
                );
            }}
        </GamesFetcher>
    );
}

export default SlotGames;
